
.details {
    width: 1170px;
    height: 600px;
}
.buy {
    width: 1170px;
    height: 600px;
    margin: 65px auto;
    background-color:#fefefe;
}
.buy .d-img{
    width:470px;
}
.show-img {
    position: relative;
    float: left;
    width: 470px;
    height: 600px;
    overflow: hidden;
}
.show-img .s-img {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform:translate(-50%,-70%);
            transform:translate(-50%,-70%);
    width: 370px;
    height: 500px;
}
.show-img .show-item {
    position: absolute;
    bottom: 0;
    width: 440px;
    height: 144px;
}
.show-img .show-item .img-item {
    display: inline-block;
    width: 100px;
    height: 144px;
    margin-right:5px;
}
.d-items {
    position:relative;
    float: right;
    width: 670px;
    height: 800px;
    margin-top:20px;
}
.d-items .fg-name {
    font-size: 30px;
    height: 50px;
}
.shop {
    height: 400px;
    width: 670px;
}
.shop :nth-child(even) {
    border-bottom: 1px solid #f4f4f4;
}
.shop-item {
    width: 670px;
    height: 35px;
    overflow: hidden;
    font-size: 16px;
    line-height: 30px;
}
.shop-item > span {
    font-size: 18px;
    line-height: 30px;
}
.shop-item > p {
    display: inline-block;
    font-size: 16px;
    line-height: 30px;
}
.ls-num {
    height: 70px;
}
.s-num {
    display: inline-block;
    margin-top: 30px;
}
.shop-item .shop-num {
    top: 10px;
}
.buy-button {
    margin-top: 20px;
    height: 70px;
    width: 670px;
    border-bottom: 1px solid #f4f4f4;
}
.infor {
    position: relative;
    width: 1170px;
    margin: 60px auto 0;
}
.infor .el-tabs .el-tabs__content{
    width:1170px;
    top:-100px;
}
.infor .el-tabs .el-tabs__content .el-tab-pane{
    width:100%;
}
.infor .el-tabs .el-tabs__content .el-tab-pane>img{
    display:block;
    margin:0 auto;
}
.relate {
    position:relative;
    width: 1170px;
    height: 560px;
    margin:0 auto;
    overflow: hidden;
}
.relate > h3 {
    width: 1170px;
    height: 60px;
    font-size: 28px;
    font-weight: normal;
    text-align: center;
}
.card {
    display: inline-block;
    width: 270px;
    height: 460px;
    text-align: center;
    margin: 8px;
}
img + h3 {
    width: 270px;
    margin: 15px auto;
    font-size: 14px;
    font-weight: normal;
}
.star {
    margin: 10px;
}
.description{
    font-size:18px;
    line-height: 25px;
}



